// CUSTOM HOOK: useGetData
// Nhận vào: 
//    -- url: Địa chỉ api cần get dữ liệu
//    -- callback function : hàm sẽ được gọi sau khi get dữ liệu thành công
//    -- config : các thiết lập bổ sung
// Xuất ra:
//    -- status : Trạng thái load gồm: 'loading', 'ok', false
//    -- sendRequest : Hàm kích hoạt gửi data

import { useState, useCallback } from 'react';

const useGetData = (url, callback, config = null) => {
    // Default setup
    let option = {
        method: 'GET'
    }

    // Nếu có config truyền vào, gộp vào option
    if (config && (typeof config === 'object')) {
        option = {
            ...option,
            ...config
        }
    }

    // Thiết lập trạng thái
    // Nếu đang loading: status -> loading
    // Nếu load xong: status -> ok
    // Nếu error: status -> false
    const [status, setStatus] = useState(null);

    // Gửi data
    const sendRequest = useCallback(async () => {
        // Chuyển trạng thái về loading
        setStatus('loading');
        try {
            const response = await fetch(url, option);

            if (!response.ok) {
                throw new Error('Request failed!');
            }

            const data = await response.json();
            callback(data);
        } catch (err) {
            console.log(err.message);
            setStatus(false);
        }
        setStatus('ok');
    }, [url, callback, config]);

    return {
        status,
        sendRequest
    }
}

export default useGetData;